export const EVENT_RS_START = 'RetailServerRequestStart';
export const EVENT_RS_STOP = 'RetailServerRequestStop';

/**
 * Additional properties to send along a retial server request & response
 */
export interface IEventRetailServerRequestProperties {
    /**
     * HTTP Method
     */
    m: string;
    /**
     * API Endpoing
     */
    e: string;
    /**
     * Request ID (+ vector)
     */
    rv: string | undefined;
    /**
     * Request ID
     */
    r: string;
}

export interface IEventRetailServerRequestMeasurements {
    /**
     * Elapsed time in ms
     */
    t: number;
}
